*{
  margin: 0;
  padding: 0;
  font-size: 1rem;
  box-sizing: border-box;
  font-family: 'Inter', sans-serif;
}

@media only screen and (max-width : 913px ) {
  /* MainComponent - CSS */
.App{
  height: 100vh;
  background-color: hsl(36, 100%, 99%);
}
.App-black{
  height: 100vh;
  background: rgb(0,0,0,0.5);
}



/* Navbar - CSS */
.navbar{
  max-width: 21.8rem;
  margin: 0 auto;
  height: 5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.nav-link{
  margin: 1rem 1.5rem;
  list-style: none;
}
.link{
  color: hsl(240, 100%, 5%);
  text-decoration: none;
}
.link:hover{
  color: hsl(5, 85%, 63%);
}
.close-button{
  padding: 0rem 0 1rem 6.5rem;
  border: none;
  background: none;
  margin-bottom: 2rem;
}
.button-on{
  display: none;
  flex-direction: column;
  position: absolute;
  width: 80%;
  height: 100vh;
  top: 0;
  left: 1000px;
  opacity: 0;
  transition: 0.5s ease;
  background: hsl(36, 100%, 99%);
  z-index: 1;
}
.button-off{
  padding-top: 1.8rem;
  display: flex;
  flex-direction: column;
  position: absolute;
  width: 80%;
  height: 100vh;
  top: 0;
  left: 140px;
  transition: 0.5s ease;
  background: hsl(36, 100%, 99%);
  z-index: 1;
}
.menu-on,  .menu-off{
  border: none;
  background: none;
}
/* Navbar - CSS */


/* main-component */
.main-component{
  max-width: 21.8rem;
  margin: 0 auto;
}
.image-responsive{
  width: 21.8rem;
  height: 20rem;
  background-size: cover;
  background-image: url(./assets/image-web-3-mobile.jpg);
}
.image-responsive-black{
  width: 21.8rem;
  height: 20rem;
  background-size: cover;
  background-color: black;
  z-index: -1;
  background-image:  linear-gradient(
    rgba(0, 0, 0, 0.5),
    rgba(0, 0, 0, 0.5)
  ), url(./assets/image-web-3-mobile.jpg);
}
.content-text{
  margin: 1rem 0;
}
.text-heigh{
  margin: 1rem 0;
  font-size: 50px;
}
.text-paragraph{
  line-height: 1.6rem;
}
.button-main{
  width: 70%;
  color: hsl(36, 100%, 99%);
  border: none;
  background-color: hsl(5, 85%, 63%);
  padding: 0.8rem;
  margin: 1.5rem 0 2rem 0;
}
.button-main:active{
  background-color: hsl(240, 100%, 5%);
}
/* main-component */


/* side-component */
.side-component{
  background-color: hsl(240, 100%, 5%);
  max-width: 21.8rem;
  margin: 0 auto;
}
.inner-text-side{
  padding: 2rem;
}
.main-text-side{
  color: hsl(35, 77%, 62%);
  font-size: 2rem;
  margin-bottom: 1rem;
}
.first-text-side{
  padding: 1rem 1rem 2rem 0;
  border-bottom: 1px solid hsl(236, 13%, 42%);
  line-height: 1.8rem;
}
.second-text-side{
  padding: 1rem 1rem 2rem 0;
  border-bottom: 1px solid hsl(236, 13%, 42%);
  line-height: 1.8rem;
}
.third-text-side{
  padding: 1rem 1rem 0rem 0;
  line-height: 1.8rem;
}
.link-text-side{
  color:hsl(36, 100%, 99%);
  font-weight: bold;
  text-decoration: none;
}
.link-text-side:active{
  color: hsl(35, 77%, 62%);

}
.detail-text-side{
  font-size: 14px;
  color: hsl(36, 100%, 99%);
}
/* side-component */

/* footer - CSS */
footer{
  max-width: 21.8rem;
  margin: 0 auto;
  margin-bottom: 1rem;
}
.footer-content{
  display: flex;
  margin-top: 2rem;
}
.footer-image{
  width: 28%;
}
.footer-content-01{
  margin: 0 1rem;
  line-height: 1.5rem;
}
.footer-content-02{
  margin: 0 1rem;
  line-height: 1.5rem;
}
.footer-content-03{
  margin: 0 1rem;
  line-height: 1.5rem;
}
.footer-main-number{
  color: hsl(233, 8%, 79%);
  font-size: 2rem;
  margin-bottom: 1.5rem;
}
.content-title{
  margin: 1rem 0;
  color: hsl(240, 100%, 5%);
  font-weight: bold;
  text-decoration: none;
}
.content-title:active{
  color: hsl(5, 85%, 63%);
}
.footer-detail{
  font-size: 13px;
}

}

 

